import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";
import "swiper/css/pagination";
import { Autoplay, Pagination} from "swiper";
import TopBannerItemLoading from "./Loading/TopBannerItemLoading";

function TopBanner(props){
    return(
        <div className="px-4 xl:px-0">
    <Swiper pagination={true} loop={true} centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }} modules={[Autoplay ,Pagination]} className="mySwiper w-full h-fit lg:h-[420px] rounded">
          <SwiperSlide><TopBannerItemLoading img="0223.jpg" link="/cat/women/Blazers" /></SwiperSlide>
          <SwiperSlide><TopBannerItemLoading img="bc-homepage.jpg" link="/cat/men/T-Shirts" /></SwiperSlide>
    </Swiper>
      
    </div>
    )
}

export default TopBanner;