import React, {useState} from 'react'
import {Link} from 'react-router-dom';
import ScrollTopAction from '../../Action/ScrollTopAction';

function TopBannerItemLoading(props){
    let [load, setLoad] = useState(false);
    return(
        <>
        
                    <Link to={`${props.link}`} onClick={ScrollTopAction} className={`${load ? 'block' : 'hidden'}`}>
                    <img src={`./img/${props.img}`} onLoad={()=>{setLoad(true)}} alt="" className="w-full object-cover object-top h-full rounded" />
                    </Link>       


                    <div className={`w-full h-[212px] md:h-[345px] lg:h-[420px]  rounded relative anim-top ${!load ? 'block' : 'hidden'}`}>
                    <div className="absolute bottom-0 top-0 my-auto right-12 p-2 h-fit rounded w-1/3 flex flex-col justify-between text-white text-center">
                    <span className="bg-gray-200 rounded h-4 md:h-8 lg:h-12 w-1/4 my-1 lg:my-1"></span>
                    <span className="bg-gray-200 rounded h-8 md:h-10 lg:h-16 w-4/4 my-0 lg:my-1"></span>
                    </div>
                    </div>
                         
            
        </>
    )
}

export default TopBannerItemLoading;